.modal {
  display: flex;
  justify-content: center;
  align-items: center;

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  color: var(--main-bg-color);
  background-color: rgba(0, 0, 0, .5);

  visibility: hidden;
  opacity: 0;
  transition: visibility .1s linear 0s, opacity .3s linear 0s;
}
